body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
  color: #404040;
  background-color: #fafafa;
  background-size: 100% 1.2em;
  background-image: -webkit-linear-gradient(0deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px), -webkit-linear-gradient(#eee .05em, transparent .05em);
  background-image: -moz-linear-gradient(0deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px), -moz-linear-gradient(#eee .05em, transparent .05em);
  background-image: -ms-linear-gradient(0deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px), -ms-linear-gradient(#eee .05em, transparent .05em);
  background-image: -o-linear-gradient(0deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px), -o-linear-gradient(#eee .05em, transparent .05em);
  background-image: linear-gradient(0deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px), linear-gradient(#eee .05em, transparent .05em);
}

svg {
  font-weight: 500;
}

h1, h3, h4 {
  font-weight: 400;
}

h2 {
  font-size: 1.67rem;
  font-weight: 200;
  text-transform: uppercase;
}

h5 {
  font-weight: 300;
  opacity: 0.8;
}

a {
  color: darkorange;
}

a:hover {
  color: navy;
}

.jumbotron {
  background-color: #fefefe;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
  border-bottom: 1px solid #eee;
  box-shadow: 3px 5px 5px 0px #efefef;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.control-panel {
  margin: 32px;
}

.control-panel button {
  margin-right: 8px;
  margin-bottom: 16px;
}

.card-subtitle {
  margin-top: 4px;
}

.card-text {
  margin-top: 16px;
}

.card-img-container {
  margin-bottom: 16px;
  opacity: 0.9;
  border: 1px solid #f0f0f0;
  box-shadow: 0px 2px 2px 0px #efefef;
}

.btn-primary {
  background-color: slategray;
  border: none;
}

.btn-primary:hover {
  background-color: slategray;
}

.btn-primary:active,
.btn-primary:focus,
.btn-primary.focus,
.btn-primary.active,
.btn-primary[disabled] {
  background-color: slategray !important;
}

svg {
  margin-top: 32px;
}

svg circle:hover {
  cursor:pointer;
}

.paragraph {
  margin-left: 16px;
  margin-right: 16px;
  margin-top: 8px;
  margin-bottom: 8px;
  line-height: 2.0rem;
}

.chart-container {
  margin-top: 16px;
}
.chart-container .chart-description {
  margin: 16px;
}

.main-content {
  padding-top: 32px;
  padding-bottom: 64px;
  border: 1px solid;
  border-top:0;
  border-color: #eee;
  margin-left: 64px;
  margin-right: 64px;
  margin-bottom: 0;
  padding-left: 16px;
  padding-right: 16px;
  background-color: white;
}

.main-content h2 {
  margin-top: 48px;
  margin-bottom: 24px;
}

.main-content a {
  color: navy;
  font-weight: 500;
}

.main-content a:hover {
  color: darkorange;
}

div.tooltip {
  position: absolute;
  text-align: center;
  width: 192px;
  height: 98px;
  padding: 4px;
  background-color: #fafafa;
  border: 1px solid #dedede;
  pointer-events: none;
  border-radius: 3px;
  box-shadow: 1px 2px 2px 0px #dadada;
  padding-top: 8px;
}

div.tooltip .tooltip-subtitle {
  font-size: 0.75rem;
}

#chart-select {
  margin-top: 16px;
  margin-bottom: 16px;
}

.jumbotron.-as-footer {
  background-color: slategray;
  margin-bottom: 0;
  margin-top: 0;
  color: white;
  bottom: 0px;
}
.jumbotron.-as-footer .social-link {
  color: white;
  font-size: 2.25rem;
  margin-right: 16px;
}
.jumbotron.-as-footer div.row {
  margin-bottom: 32px;
}

strong {
  font-weight: bold;
}

.main-chart-container {
  margin-bottom: 64px;
}

.card {
  border: 0;
}

.form-group label {
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0;
}

a#mips-link:hover,
a#tdp-link:hover {
  cursor: pointer;
}

.row {
  margin-bottom: 8px;
}

.annotation-container {
  border: 1px solid rgb(128, 128, 128, 0.2);
  padding: 8px;
  border-radius: 5px;
  box-shadow: 3px 5px 5px 0px #efefef;
}

.bar-element:hover {
  cursor: pointer;
}

.attribution {
  font-size: small;
}